<template>
  <li :class="statusClassName">
    <button
      type="button"
      class="btn_step"
      :disabled="statusClassName == 'disabled'"
      @click="$emit('onChangeStep',stepItem.id)">
      <span :class="['ico_zippro', 'ico_step_'+stepItem.id.toLowerCase()]"></span>
    </button>
  </li>
</template>

<script>
export default {
  name:'StepperItem',
  props:{
    stepItem: Object,
    currentStep: Object,
  },
  computed:{
    statusClassName(){
      if(this.stepItem.index == this.currentStep.index){
        return 'active';
      }else if(this.stepItem.index >= this.currentStep.index){
        return 'disabled';
      }else{
        return 'complete';
      }
    }
  }
}
</script>
<style scoped>
li{position:relative;display:inline-block;padding-left:calc(25% - 50px);vertical-align:top}
li:first-child{padding-left:0}

/* 완료된 */
.btn_step{display:block;width:40px;height:40px;border:2px solid #F9B9C9;border-radius:20px;background-color:#fff;color:#555;text-align:center;box-shadow:1px 1px 4px 0 rgba(0,0,0,.1)}
li:after{position:absolute;top:19px;left:0;right:40px;height:2px;background-color:#F9B9C9;content:''}
li:first-child:after{content:none}
/* 비활성화 */
li.disabled:after{background-color:#ddd}
li.disabled .btn_step{border:2px solid #ddd;background-color:#ddd;box-shadow:none}
/* 활성화 */
li.active:after{background-color:#F9B9C9}
li.active .btn_step{margin-top:0;border:2px solid #ED164B;color:#fff;box-shadow:2px 2px 4px 0 rgba(107,58,70,.2)}

.btn_step .ico_zippro{margin:5px 0}
</style>