<template>
  <InputBase :class="{ disabled: isDisabled, tf_error: isError, tf_border: isBorderStyle }">
    <input 
      v-model="input" 
      :type="type" 
      :placeholder="placeholder" 
      :maxLength="maxLength"
      :disabled="isDisabled"
      ref="input"
      KeyboardEvent.key:keyup.13="onKeyUpEnter"
      @focus="onFocus"
      @blur="onBlur"
      @input="onInput" />
  </InputBase>
</template>

<script>
import InputBase from '@/components/common/input/InputBase';

import { changeOnlyKorEng , changeOnlyKorEngSpace } from '@/utils/stringUtils';

export default {
  name: 'Input',
  components: {
    InputBase
  },
  props: {
    value: String,
    type: {
      type: String,
      default: 'text'
    },
    isBorderStyle:{
      type: Boolean,
      default: false
    },
    placeholder: {
      type: String,
      default: null
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isError: {
      type: Boolean,
      default: false
    },
    maxLength: Number,
    isFocusOnMount: {
      type: Boolean,
      default: false
    },
    isOnlyEngKor: {
      type: Boolean,
      default: false,
    },
    isOnlyEngKorSpace : {
      type: Boolean,
      defualt: false,
    }
  },
  computed: {
    input: {
      get() {
        if (this.isOnlyEngKor) {
          const validText = changeOnlyKorEng( this.value );

          if( validText !== this.value ) {
            this.$emit("onChange", validText );

            this.$emit("update:value", validText );
          }
        }

        if(this.isOnlyEngKorSpace) {
          const validText = changeOnlyKorEngSpace( this.value );

          if( validText !== this.value ) {
            this.$emit("onChange", validText );

            this.$emit("update:value", validText );
          }
        }

        if( this.maxLength && this.value.length > this.maxLength ) {
          this.$emit("onChange", this.value.slice( 0, this.maxLength ));

          this.$emit("update:value", this.value.slice( 0, this.maxLength ));
        }

        return this.value;
      },
      set(value) {
        this.$emit("update:value", value);
      }
    }
  },
  mounted() {
    if( this.isFocusOnMount ) {
      this.$refs.input.focus();
    }
  },
  methods: {
    blur() {
      // 외부에서 호출 될 메서드.
      this.$refs.input.blur();
    },
    onFocus( e ) {
      this.$emit("update:focus", e);
    },
    onBlur( e ) {
      this.$emit("update:blur", e);
    },
    onKeyUpEnter( e ) {
      this.blur();

      this.$emit("onEnterKey", e);
    },
    onInput(e) {
      this.$emit("onInput", e);
    }
  }
}
</script>
