import SingupModel from '@/views/signup/model/SingupModel';

export default class SingupViewModel {
  constructor() {
    this.model = {};

    // step
    this.stepDataList = [
      { index: 1, name: '회원유형', id: 'Type' },
      { index: 2, name: '약관', id: 'Term', validateDataList:['servicePolicy', 'privacyPolicy'] },
      { index: 3, name: '소셜가입유형', id: 'Social' },
      { index: 4, name: '기본정보입력', id: 'BasicForm', validateDataList:['userName','email','password','passwordCheck','phone'] },
      { index: 5, name: '신청완료', id: 'Complete' },
    ];
    // step - 활성화된 step
    this.currentStep = { index: 1, name: '회원유형', id: 'Type' };

    // 회원유형
    this.typeDataList = [
      { id: 'consultant', title: '컨설턴트', desc: '부동산 컨설팅 활동을 하고있는 분' },
      { id: 'realEstateAgent', title: '공인중개사', desc: '공인중개사로 활동을 하고있는 분' },
      { id: 'salesOffice', title: '분양사무소', desc: '현장을 가지고 분양을 하고있는 분' },
    ];

    // 약관
    this.termDataList = [
      { id: 'servicePolicy', text: '집 Pro 서비스 이용 약관', isEssential: true },
      { id: 'privacyPolicy', text: '개인정보수집 및 이용에 대한 동의', isEssential: true },
      { id: 'marketingPolicy', text: '(선택) 이벤트 및 혜택알림 수신 동의', isEssential: false },
    ],
    // 약관 - 전체동의 여부
    this.isAllAgree = false;
    // 약관 - 약관팝업
    this.isTermPopup = false;
    // 약관 - 약관팝업 타입
    this.termPopupType = '';

    // 소셜가입유형
    this.socialDataList = [
      { id: 'naver', btnStyle: 'naver', text: '네이버 계정으로 가입', isOauth: true },
      { id: 'kakao', btnStyle: 'kakao', text: '카카오 계정으로 가입', isOauth: true },
      { id: 'goole', btnStyle: 'goole', text: 'Google 계정으로 가입', isOauth: true },
      { id: 'idpw', btnStyle: 'tertiary', text: 'ID/PW 회원가입', isOauth: false },
    ];

    // 기본정보입력 - 에러
    this.isErrorEmail = false;
    this.isErrorPassword = false;
  }

  setModel(){
    this.model = new SingupModel();
  }

  // step - id로 변경
  onChangeSignupMode(changedId){
    this.currentStep = this.stepDataList.find(step => step.id == changedId)
  }

  // step - next step으로 변경
  onChangeSignupNext(){
    if(!this.validated()) return;
    const nextIndex = this.currentStep.index+1;
    this.currentStep = this.stepDataList.find(step => step.index == nextIndex)
  }

  // (공통) 검증
  validated(){
    // 검증 내역이 없을 시
    if(!this.currentStep.validateDataList) return true;
    let isValidated = true;
    // 각 step별 검증내역 데이터 유무 검증
    this.currentStep.validateDataList.forEach(validateData => {
      if(!this.model[validateData]){
        isValidated = false;
        return false;
      }
    });
    return isValidated;
  }

  // 회원유형 - 선택 시
  onClickType( selectedType ){
    this.model.type = selectedType;
    this.onChangeSignupNext();
  }

  // 약관 - 전체동의 체크 시
  onClickedAll(isAllAgree){
    this.termDataList.forEach(termData => {
      this.model[termData.id] = isAllAgree;
    })
  }

  // 약관 - 항목 체크 시 전체동의 적용
  onClickedItem(){
    let isAllItemAgree = true;
    this.termDataList.forEach(termData => {
      if(!this.model[termData.id]){
        isAllItemAgree = false;
      }
    });
    this.isAllAgree = isAllItemAgree && !this.isAllAgree;
  }

  // 약관 - 약관팝업 열기
  onClickTermPopup( termPopupType ){
    this.termPopupType = termPopupType;
    this.isTermPopup = true;
  }

  // 약관 - 약관팝업 닫기
  onClickCloseTermPopup(){
    this.isTermPopup = false;
  }

  // 약관 - 약관팝업 동의
  onClickCompleteTermPopup(){
    this.model[this.termPopupType] = true;
    this.onClickedItem();
    this.isTermPopup = false;
  }

  // 소셜가입유형 - 유형 선택시
  onClickSocail(seletedSocialData){
    this.model.socialType = seletedSocialData.id;
    if(!seletedSocialData.isOauth){
      this.onChangeSignupNext();
    }else{
      this.OauthSignUp(seletedSocialData.id);
    }
  }
  // 소셜가입유형 - OAuth 회원가입 Redirect
  OauthSignUp(OauthType){
    console.log(OauthType,'API Response URL Redirect');
  }

  // 기본정보입력 - 검증
  basicformValidated(){
    const { email, password, passwordCheck } = this.model;
    // 이메일 검증 및 에러
    const isCorrectEmail = !email || email.indexOf('@') > -1;
    this.isErrorEmail = !isCorrectEmail;
    // 비밀번호 매치 검증 및 에러
    const isMatchPassword = !passwordCheck || password == passwordCheck;
    this.isErrorPassword = !isMatchPassword;

    return isCorrectEmail && isMatchPassword;
  }

  // 기본정보입력 - 회원가입완료
  signupComplete(){
    console.log('회원가입 API 호출')
    // 성공 : 완료 페이지로 이동
    this.onChangeSignupNext();
    // 실패 : 알럿으로 에러문구 적용 .. 설계필요
  }
}