<template>
  <PageWithLayout>
    <Stepper
      :dataList="viewModel.stepDataList"
      :currentStep="viewModel.currentStep"
      @onChangeStep="id => viewModel.onChangeSignupMode(id)"/>

    <component
      :is="'Signup'+viewModel.currentStep.id"
      :viewModel="viewModel">
    </component>

    <template v-slot:popup>
      <TermPopup
        v-if="viewModel.isTermPopup"
        :viewModel="viewModel"/>
    </template>
  </PageWithLayout>
</template>

<script>
import PageWithLayout from '@/components/layout/PageWithLayout'
import Stepper from '@/components/common/stepper/Stepper';
import SignupType from '@/views/signup/view/components/SignupType';
import SignupTerm from '@/views/signup/view/components/SignupTerm';
import SignupSocial from '@/views/signup/view/components/SignupSocial';
import SignupBasicForm from '@/views/signup/view/components/SignupBasicForm';
import SignupComplete from '@/views/signup/view/components/SignupComplete';
// popup
import TermPopup from '@/views/signup/view/popup/TermPopup';
// viewModel
import SingupViewModel from '@/views/signup/viewModel/SingupViewModel';

export default {
  name:'Signup',
  components:{
    PageWithLayout,
    Stepper,
    SignupType,
    SignupTerm,
    SignupSocial,
    SignupBasicForm,
    SignupComplete,

    TermPopup
  },
  beforeMount(){
    this.viewModel.setModel();
  },
  data(){
    return{
      viewModel: new SingupViewModel(),
    }
  },
}
</script>
<style scoped>
>>> .tit_signup{display:inline-block;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:22px;line-height:24px;color:#222;vertical-align:top}
>>> .desc_signup{margin-bottom:20px;font-size:14px;line-height:20px;color:#555;letter-spacing:-0.02em}

/* 모바일 */
@media all and (max-width:600px){
 :deep() .desc_signup{margin-bottom:14px}
}
</style>