<template>
  <div>
    <CheckboxOnlyOne
      id="allAgree"
      text="전체동의"
      :isLarge="true"
      v-model:isSelected="viewModel.isAllAgree"
      @update:isSelected="isSelected => viewModel.onClickedAll(isSelected)" />
    <ul class="list_policy">
      <li v-for="termData in viewModel.termDataList" :key="termData.id">
        <CheckboxOnlyOne
          :id="termData.id"
          :text="termData.text"
          v-model:isSelected="viewModel.model[termData.id]"
          @update:isSelected="viewModel.onClickedItem()" />
        <button
          class="btn_policy_deail"
          @click="viewModel.onClickTermPopup(termData.id)">자세히</button>
      </li>
    </ul>
    <div class="group_btn">
      <Button
        btnSize="large"
        btnStyle="primary"
        text="다음"
        :disabled="isInValidate"
        @onClickBtn="viewModel.onChangeSignupNext()" />
    </div>
  </div>
</template>

<script>
import CheckboxOnlyOne from '@/components/common/checkbox/CheckboxOnlyOne';
import Button from '@/components/common/button/Button';

export default {
  name:'SignupTerm',
  components:{
    CheckboxOnlyOne,
    Button
  },
  props:{
    viewModel: Object,
  },
  mounted(){
    this.viewModel.onClickedItem();
  },
  computed:{
    isInValidate(){
      return !this.viewModel.validated();
    }
  },
}
</script>
<style scoped>
.list_policy{margin-top:18px;padding:0 14px}
.list_policy li{overflow:hidden}
.list_policy li .check_comm{float:left}
.list_policy li .btn_policy_deail{float:right;color:#888;text-decoration:underline;}
.list_policy li + li{margin-top:8px}
</style>