export default class SingupModel {
  constructor() {
    // 회원유형
    this.type = '';

    // 약관
    this.servicePolicy = false;
    this.privacyPolicy = false;
    this.marketingPolicy = false;

    // 소셜가입유형
    this.socialType = '';

    // 기본정보
    this.userName = '';
    this.email = '';
    this.password = '';
    this.passwordCheck = '';
    this.phone = '';
  }
}