<template>
  <div class="area_stepper">
    <ul class="list_stepper">
      <StepperItem
        v-for="stepItem in dataList"
        :key="stepItem.index"
        :stepItem="stepItem"
        :currentStep="currentStep"
        @onChangeStep="id => $emit('onChangeStep',id)" />
    </ul>
    <div class="head_stepper">
      <span class="txt_stepper">step{{ currentStep.index }}</span>
      <h3 class="tit_stepper">{{ currentStep.name }}</h3>
    </div>
  </div>
</template>

<script>
import StepperItem from '@/components/common/stepper/StepperItem';

export default {
  name:'Stepper',
  components:{
    StepperItem
  },
  props:{
    dataList: Array,
    currentStep: Object
  },
  watch:{
    currentStep(newVal, oldVal){
      if(newVal != oldVal){
        window.scrollTo(0, 0);
      }
    }
  }
}
</script>
<style scoped>
.area_stepper{padding:40px 0 20px}
.area_stepper:after{display:block;visibility:hidden;height:0;font-size:0;clear:both;content:''}

.head_stepper{margin-top:20px}
.txt_stepper{display:block;font-size:12px;line-height:14px;color:#555}
.tit_stepper{display:block;margin-top:4px;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:20px;line-height:28px;color:#222;letter-spacing:-0.02em}

.list_stepper{width:100%}
.list_stepper:after{display:block;visibility:hidden;height:0;font-size:0;clear:both;content:''}

/* 모바일 */
@media all and (max-width:600px){
  .area_stepper{padding:20px 0 40px}
}
</style>