export function getCanConvertNumber( value ) {
  if( value === 0 ) return true;

  if( !value )  return false;

  return !isNaN( value );
}

export function addCommaForMoney( value ) {
  if( value === 0 ) return '0';
  
  if( !value )  return value;

  var strMoney = String( value );

  if( strMoney.indexOf( '.' ) > -1 ){
    var decimal = strMoney.split('.')[1];
    var strMoney = String( value ).split('.')[0];
  }

  if( strMoney.indexOf( ',' ) > -1 ) return strMoney;

  let ret = '';
  let nFromEnd;
  let char;

  for(var i=0; i<strMoney.length; ++i) {
    char = strMoney[ i ];

    nFromEnd = strMoney.length - 1 - i;

    ret += char;
    if( nFromEnd !== 0 && nFromEnd % 3 === 0 && char !== '-' ) {
      ret += ','
    }
  }

  if( decimal ){
    return ret +'.'+ decimal;
  }else{
    return ret
  }
}