<template>
  <Popup
    :title="termTitle"
    closeBtnText="닫기"
    compeleteBtnText="동의"
    @onClickClose="viewModel.onClickCloseTermPopup()"
    @onClickComplete="viewModel.onClickCompleteTermPopup()">
    <div class="term_box" v-html="termContents">
    </div>
  </Popup>
</template>

<script>
import Popup from '@/components/layout/popup/Popup';
import { servicePolicy, privacyPolicy, marketingPolicy } from '@/constants/terms';

export default {
  name: 'TermPopup',
  components:{
    Popup,
  },
  props:{
    viewModel: Object
  },
  computed:{
    termTitle(){
      switch(this.viewModel.termPopupType){
        case 'servicePolicy' : return servicePolicy.title;
        case 'privacyPolicy' : return privacyPolicy.title;
        case 'marketingPolicy' : return marketingPolicy.title;
      }
    },
    termContents(){
      switch(this.viewModel.termPopupType){
        case 'servicePolicy' : return servicePolicy.content;
        case 'privacyPolicy' : return privacyPolicy.content;
        case 'marketingPolicy' : return marketingPolicy.content;
      }
    }
  }
}
</script>

<style scoped>
.term_box{overflow:hidden;}
.term_box:deep() h5{margin-top:8px;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:14px;line-height:16px;color:#222}
.term_box:deep() h6{margin-top:8px;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:12px;line-height:14px}
.term_box:deep() p{margin-top:4px;font-size:12px;line-height:16px}
</style>