<template>
  <CheckboxBase :class="{ check_comm_large : isLarge }">
    <input type="checkbox" 
      :id="id" 
      :value="id" 
      v-model="input" 
      :disabled="isDisabled">
    <label :for="id"><span class="ico_zippro ico_checkbox"></span>{{ text }}</label>
  </CheckboxBase>
</template>

<script>
import CheckboxBase from './CheckboxBase'

export default {
  name: 'CheckboxOnlyOne',
  components: {
    CheckboxBase
  },
  props: {
    id: String,
    isSelected: Boolean,
    text: String,
    isDisabled: {
      type: Boolean,
      default: false
    },
    isLarge:{
      type: Boolean,
      default: false
    }
  },
  computed: {
    input: {
      get() {
        return this.isSelected ? [ this.id ] : [];
      },
      set(value) {
        const isSelected = value.length > 0;
        this.$emit("update:isSelected", isSelected);
      }
    }
  }
}
</script>
