<template>
  <div>
    <dl class="list_from_s">
      <dt>이름<span class="ico_zippro ico_star">필수항목</span></dt>
      <dd>
        <Input
          :maxLength="20"
          :isBorderStyle="true"
          placeholder="이름을 입력해주세요"
          v-model:value="viewModel.model.userName"
          :isFocusOnMount="true" />
      </dd>
      <dt>E-mail<span class="ico_zippro ico_star">필수항목</span></dt>
      <dd>
        <Input
          :isBorderStyle="true"
          placeholder="E-mail을 입력해주세요"
          v-model:value="viewModel.model.email"
          :isError="viewModel.isErrorEmail" />
      </dd>
      <dt>비밀번호<span class="ico_zippro ico_star">필수항목</span></dt>
      <dd>
        <Input
          type="password"
          :isBorderStyle="true"
          placeholder="비밀번호를 입력해주세요"
          v-model:value="viewModel.model.password" />
      </dd>
      <dt>비밀번호 확인<span class="ico_zippro ico_star">필수항목</span></dt>
      <dd>
        <Input
          type="password"
          :isBorderStyle="true"
          placeholder="비밀번호를 한번 더 입력해주세요"
          v-model:value="viewModel.model.passwordCheck"
          :isError="viewModel.isErrorPassword" />
      </dd>
      <dt>전화번호<span class="ico_zippro ico_star">필수항목</span></dt>
      <dd>
        <InputTel
          :isBorderStyle="true"
          :useMobile="false"
          :useArea="true"
          placeholder="- 없이 입력"
          v-model:value="viewModel.model.phone"/>
      </dd>
    </dl>
    <div class="group_btn">
      <Button
        btnSize="large"
        btnStyle="primary"
        text="완료"
        :disabled="isInValidate"
        @onClickBtn="viewModel.signupComplete()" />
    </div>
  </div>
</template>

<script>
import Input from '@/components/common/input/Input';
import InputTel from '@/components/common/input/InputTel';
import Button from '@/components/common/button/Button';

export default {
  name:'SignupBasicForm',
  components:{
    Input,
    InputTel,
    Button
  },
  props:{
    viewModel: Object,
  },
  computed:{
    isInValidate(){
      return !this.viewModel.basicformValidated() || !this.viewModel.validated();
    }
  },
}
</script>