<template>
  <InputBase :class="{ disabled: isDisabled, tf_error: isError, tf_border: isBorderStyle }">
    <input 
      v-model="input" 
      :placeholder="placeholder" 
      :disabled="isDisabled"
      @focus="onFocus"
      @blur="onBlur"
      ref="input"
      KeyboardEvent.key:keyup.13="onKeyUpEnter" />
  </InputBase>
</template>

<script>
import InputBase from '@/components/common/input/InputBase'
import { getIsValidText, getValidText } from '@/utils/phoneUtils'

export default {
  name: 'Input',
  components: {
    InputBase
  },
  props: {
    value: String,
    placeholder: String,
    isBorderStyle:{
      type: Boolean,
      default: false
    },
    isDisabled: {
      type: Boolean,
      default: false
    },
    isError: {
      type: Boolean,
      default: false
    },
    maxLength: Number
  },
  computed: {
    input: {
      get() {
        if( !getIsValidText( this.value ) ) {
          this.$emit("update:value", getValidText( this.value ) );
        }
        
        if( this.maxLength && this.value.length > this.maxLength ) {
          this.$emit("update:value", this.value.slice( 0, this.maxLength ));
        }

        return this.value;
      },
      set(value) {
        this.$emit("update:value", value);
      }
    }
  },
  methods: {
    blur() {
      // 외부에서 호출 될 메서드.
      this.$refs.input.blur();
    },
    onFocus( e ) {
      this.$emit("update:focus", e);
    },
    onBlur( e ) {
      this.$emit("update:blur", e);
    },
    onKeyUpEnter() {
      this.blur();
    }
  }
}
</script>
