<template>
  <div class="zippro_layer" @click.self="$emit('onClickClose')"><!--  v-scroll-lock="isModalMode" -->
    <div
      :class="['inner_comm_layer',{ inner_comm_noborder : !isBorder, inner_comm_mo_layer : isLayer }]">
       
      <!-- :style="{ maxWidth : `${maxWidth}px`, maxHeight: `${maxHeight}px`,}" -->
      <div class="layer_head">
        <strong class="tit_layer">{{ title }}</strong>
      </div>
      <div class="layer_body" :style="{ height : isHeightFit ? 'inherit' : '' }" ref="layerBody">
         <!-- maxWidth : `${maxWidth}px`, maxHeight: `${innerMaxHeight}`,  -->
        <slot />
      </div>
      <div class="layer_foot">
        <div :class="['group_btn', { group_btn_type2 : closeBtnText && compeleteBtnText } ]">
          <Button
            v-if="closeBtnText"
            :btnSize="isMobile ? 'large' : 'small'"
            btnStyle="tertiary"
            :text="closeBtnText"
            @onClickBtn="$emit('onClickClose')" />
          <Button
            v-if="compeleteBtnText"
            :btnSize="isMobile ? 'large' : 'small'"
            btnStyle="primary"
            :text="compeleteBtnText"
            @onClickBtn="$emit('onClickComplete')" />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import Button from '@/components/common/button/Button';

export default {
  name: 'Popup',
  components:{
    Button
  },
  props:{
    mode: undefined,
    isBorder:{
      type:Boolean,
      default: true
    },
    isHeightFit:{
      type:Boolean,
      default: false
    },
    isLayer:{
      type:Boolean,
      default: false
    },
    maxWidth: Number,
    maxHeight: Number,
    title: String,
    closeBtnText: String,
    compeleteBtnText: String,
  },
  mounted(){
    document.body.style.overflow = "hidden";
  },
  unmounted(){
    document.body.style.overflow = "";
  },
  watch:{
    mode(newVal, oldVal){
      if(newVal != oldVal){
        this.$refs.layerBody.scrollTop = 0;
      }
    }
  },
  computed:{
    isMobile(){
      return this.$store.state.viewport.windowWidth <= 600;
    },
    innerMaxHeight(){
      return this.maxHeight-137 < window.outerHeight - 237 ? this.maxHeight-137+'px' : '';
    }
  }
}
</script>

<style scoped>
/* -- 레이어 팝업 -- */
.zippro_layer{position:fixed;top:0;bottom:0;left:0;right:0;z-index:9990;min-width:280px;padding:50px 0;font-size:0;background-color:rgba(34,34,34,.6);text-align:center;white-space:nowrap}
.zippro_layer:after{display:inline-block;height:100%;vertical-align:middle;content:''}

.inner_comm_layer{display:inline-block;position:relative;width:80%;max-width:650px;max-height:calc(100vh - 100px);min-height:400px;padding:30px 0 65px;border-radius:2px;background-color:#fff;white-space:normal;vertical-align:middle;box-sizing:border-box}

.layer_head,
.layer_foot{margin:0 24px}

.layer_head{padding-bottom:16px;border-bottom:1px solid #222}
.layer_head .tit_layer{display:block;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:20px;line-height:22px;text-align:left}

.layer_body{overflow:hidden;overflow-y:auto;max-height:calc(100vh - 237px);padding:20px 24px;text-align:left;box-sizing:border-box}
.layer_body:deep() .list_table_w{margin-left:20px;margin-right:20px}

.layer_foot{position:absolute;left:0;right:0;bottom:0;margin:0;padding:16px 0;border-top:1px solid #e1e1e1;background-color:rgba(255,255,255,.7);backdrop-filter:blur(3px);text-align:center}

.inner_comm_noborder .layer_head{border:0 none}
.inner_comm_noborder .layer_body{padding:0 0 20px;border:0 none}

/* 모바일 */
@media all and (max-width:600px){
  .zippro_layer{padding:0}
  .inner_comm_layer{width:100%;height: 100% !important;max-width:inherit !important;padding-bottom:73px;max-height:inherit}
  .layer_body{max-height:calc(100vh - 138px);padding:10px 20px 30px}
  .layer_body:deep() .list_table_w{margin-left:0;margin-right:0}
  .layer_foot{margin:0;padding:16px 20px}

  .inner_comm_mo_layer{position:absolute;bottom:0;left:0;height:inherit !important;border-radius:16px 16px 0 0}
}
</style>