<template>
  <ul :class="['list_type', 'list_type_'+type ]">
    <li
      v-for="typeData in dataList"
      :key="typeData.id"
       :style="horizonItemWitdh">
      <button type="button" class="btn_type" @click="onClickType(typeData)">
        <strong class="tit_type">{{ typeData.title }}</strong>
        <p v-if="typeData.desc" class="desc_type">{{ typeData.desc }}</p>
        <span class="txt_type">
          바로가기<span class="ico_zippro ico_arr_type"></span>
        </span>
      </button>
    </li>
  </ul>
</template>

<script>
export default {
  name:'TypeSelect',
  props:{
    dataList: Array,
    type:{
      type: String,
      default: 'vertical'// horizon || vertical
    }
  },
  computed:{
    horizonItemWitdh(){
      if( this.type  == 'horizon' ){
        const dataLength = this.dataList.length;
        return `width:calc(${100/dataLength}% - ${ (10*(dataLength-1))/dataLength }px)`;
      }
    }
  },
  methods:{
    onClickType(typeData){
      if( typeData.routePath ){
        this.$router.push({ path: this.$routerPath[typeData.routePath] });
      }else{
        this.$emit('onClickType',typeData.id);
      }
    }
  }
}
</script>

<style scoped>
.list_type li{display:block}
.list_type li + li{margin-top:10px}
.list_type .btn_type{display:block;width:100%;padding:24px 20px;border:1px solid #e1e1e1;text-align:left}
.list_type .btn_type:hover{border:1px solid #222}
.list_type .tit_type{display:block;font-weight:normal;font-family:'NotoSansKR_M', sans-serif;font-size:22px;line-height:24px;color:#222}
.list_type .desc_type{display:block;margin-top:4px;margin-bottom:0;font-size:14px;line-height:16px;color:#888}
.list_type .tit_type + .txt_type{margin-top:30px}
.list_type .txt_type{display:block;margin-top:10px;font-size:14px}
.list_type .txt_type .ico_arr_type{margin:5px 0 7px 4px}

.list_type_horizon li{display:inline-block;vertical-align:top}
.list_type_horizon li + li{margin:0 0 0 10px}

/* 모바일 */
@media all and (max-width:900px){
.list_type_horizon li{display:block;width:auto !important}
.list_type_horizon li + li{margin:10px 0 0 0}
}
/* 모바일 */
@media all and (max-width:600px){
  .list_type .btn_type{padding:14px 16px}
  .list_type .tit_type{font-size:18px}
}
</style>